	export default {
		data() {
			return {
				group1  : ["../../static/star.png","../../static/star.png","../../static/star.png","../../static/star.png","../../static/star.png"],
			    group2  : ["../../static/star.png","../../static/star.png","../../static/star.png","../../static/star.png","../../static/star.png"],
			    group3  : ["../../static/star.png","../../static/star.png","../../static/star.png","../../static/star.png","../../static/star.png"],
			    group4  : ["../../static/star.png","../../static/star.png","../../static/star.png","../../static/star.png","../../static/star.png"],
			    icongroup : [
					{name : "技能熟练",judge : false},
					{name : "热心负责",judge : false},
					{name : "完全速度快",judge : false},
					{name : "风雨无阻",judge : false},
					{name : "收拾废材",judge : false},
					{name : "经久耐用",judge : false}
				],
				imgroup:[],
				reqImg:[]
			}
		},
		onLoad() {
		   
		},
		methods: {
			back : function(){
			   uni.navigateBack({
			  
			   });
			},
			
			// 选中标签时触发的事件
			changesty : function(index){
				console.log(this.icongroup[index].judge);
				 if(this.icongroup[index].judge == true){
					 this.icongroup[index].judge = false;
				 }else{
					 this.icongroup[index].judge = true;
				 }
			},
			
			chagroup1 : function(index){
				
				this.group1 = [];
				
				for(var i = 0;i<=index;i++){
					this.group1.push("../../static/star_r.png");
				}
				for(var i= index;i<4;i++){
					this.group1.push("../../static/star.png");
				}	
			},
			
			chagroup2 : function(index){
				this.group2 = [];
				
				for(var i = 0;i<=index;i++){
					this.group2.push("../../static/star_r.png");
				}
				for(var i= index;i<4;i++){
					this.group2.push("../../static/star.png");
				}	
			},
			sel_img : function(){
				var that = this;
				console.log(typeof that.imgroup[0])
				if(this.imgroup.length>=9){
					uni.showToast({
						title:"最多上传9张图",
						icon:"none"
					})
					
				}else{
					uni.chooseImage({
					   count: 1, //默认9
					   sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
					   sourceType: ['album'], //从相册选择
					   success: function (res) {
						   console.log(res.tempFilePaths,res);
						   let thatRes=res;
						   if(/(.jpg|.JPG|.jpeg|.JPEG|.PNG|.png)$/.exec(res.tempFilePaths[0])){
							   uni.getFileSystemManager().readFile({
							       filePath: res.tempFilePaths[0], //选择图片返回的相对路径
							       encoding: 'base64', //编码格式
							       success: res => { //成功的回调
								   let KuoZhan=thatRes.tempFilePaths[0].split(".")
								   console.log(KuoZhan[KuoZhan.length-1])
							           let base64 = 'data:image/'+KuoZhan[KuoZhan.length-1]+';base64,' + res.data //不加上这串字符，在页面无法显示的哦
							   			that.imgroup.push(base64);
										that.reqImg.push(res.data)
							       }
							   })
						   }else{
							   uni.showToast({
							   	icon:"none",
								title:"上传格式错误！请上传jpg，jpeg，png格式的图片"
							   })
						   }
					   }
					});
				}
			},
			
			delimg : function(index){
				this.imgroup.splice(index,1);
				this.reqImg.splice(index,1);
			},
			
			chagroup3 : function(index){
				this.group3 = [];
				
				for(var i = 0;i<=index;i++){
					this.group3.push("../../static/star_r.png");
				}
				for(var i= index;i<4;i++){
					this.group3.push("../../static/star.png");
				}	
			},
			
			chagroup4 : function(index){
				this.group4 = [];
				
				for(var i = 0;i<=index;i++){
					this.group4.push("../../static/star_r.png");
				}
				for(var i= index;i<4;i++){
					this.group4.push("../../static/star.png");
				}	
			},
		 }
}
